import React, { useContext, useEffect } from 'react';
import { GlobalContext } from '../context/GlobalState';
import { MovieCard } from './MovieCard';

const defaultWatchList = [
	{
		adult: false,
		backdrop_path: '/1stUIsjawROZxjiCMtqqXqgfZWC.jpg',
		genre_ids: [12, 14],
		id: 672,
		original_language: 'en',
		original_title: 'Harry Potter and the Chamber of Secrets',
		overview:
			'Cars fly, trees fight back, and a mysterious house-elf comes to warn Harry Potter at the start of his second year at Hogwarts. Adventure and danger await when bloody writing on a wall announces: The Chamber Of Secrets Has Been Opened. To save Hogwarts will require all of Harry, Ron and Hermione’s magical abilities and courage.',
		popularity: 254.017,
		poster_path: '/sdEOH0992YZ0QSxgXNIGLq1ToUi.jpg',
		release_date: '2002-11-13',
		title: 'Harry Potter and the Chamber of Secrets',
		video: false,
		vote_average: 7.7,
		vote_count: 17196
	},
	{
		adult: false,
		backdrop_path: '/rBeBQmSdn9rUZtbJjxtEhXkEBKM.jpg',
		genre_ids: [18, 36],
		id: 7214,
		original_language: 'en',
		original_title: 'Coach Carter',
		overview:
			'Based on a true story, in which Richmond High School head basketball coach Ken Carter made headlines in 1999 for benching his undefeated team due to poor academic results.',
		popularity: 29.579,
		poster_path: '/y3HOTTyM5nLsdUzXFtFCohG28qj.jpg',
		release_date: '2005-01-14',
		title: 'Coach Carter',
		video: false,
		vote_average: 7.5,
		vote_count: 1982
	}
];

export const WatchList = () => {
	let { watchlist, addMovieToWatchlist } = useContext(GlobalContext);
	
	useEffect(() => {
		if (watchlist.length === 0) {
			defaultWatchList.forEach((movie) => {
				addMovieToWatchlist(movie);
			});
		}
	}, []);
	
	return (
		<div className="movie-page">
			<div className="container">
				<div className="header">
					<h1 className="heading">My Watchlist</h1>
					<span className="count pill">
						{watchlist.length} {watchlist.length === 1 ? 'Movie' : 'Movies'}
					</span>
				</div>
				{watchlist.length > 0 ? (
					<div className="movie-grid">
						{watchlist.map((movie) => (
							<MovieCard key={movie.id} movie={movie} type="watchlist" />
						))}
					</div>
				) : (
					<h2 className="no-movie">No movies on your watchlist, add some!</h2>
				)}
			</div>
		</div>
	);
};

export default WatchList;
