import React, { createContext, useReducer, useEffect } from 'react';
import AppReducer from './AppReducer';

// initial state
const initialState = {
	watchlist: localStorage.getItem('watchlist')
		? JSON.parse(localStorage.getItem('watchlist'))
		: [],
	watched: localStorage.getItem('watched')
		? JSON.parse(localStorage.getItem('watched'))
		: []
};

// create context
export const GlobalContext = createContext(initialState);

// provider components
export const GlobalProvider = (props) => {
	const [state, dispatch] = useReducer(AppReducer, initialState);

	useEffect(() => {
		localStorage.setItem('watchlist', JSON.stringify(state.watchlist));
		localStorage.setItem('watched', JSON.stringify(state.watched));
	}, [state]);

	// actions
	const addMovieToWatchlist = (movieToAdd) => {
		dispatch({ type: 'ADD_MOVIE_TO_WATCHLIST', payload: movieToAdd });
	};
	
	const removeMovieFromWatchlist = (movieId) => {
		dispatch({ type: 'REMOVE_MOVIE_FROM_WATCHLIST', payload: movieId });
	};
	
	const addMovieToWatched = (movieToAdd) => {
		dispatch({ type: 'ADD_MOVIE_TO_WATCHED', payload: movieToAdd });
	};

	const moveToWatchList = movie =>{
		dispatch({ type: 'MOVE_TO_WATCHLIST',payload:movie });
	}

	const removeFromWatched = (movieId) => {
		dispatch({ type: 'REMOVE_FROM_WATCHED', payload: movieId });
	};

	return (
		<GlobalContext.Provider
			value={{
				watchlist: state.watchlist,
				watched: state.watched,
				addMovieToWatchlist,
				removeMovieFromWatchlist,
				addMovieToWatched,
				moveToWatchList,
				removeFromWatched
			}}
		>
			{props.children}
		</GlobalContext.Provider>
	);
};
