import React, { useContext } from 'react';
import { GlobalContext } from '../context/GlobalState';
import Moment from 'react-moment';

export const ResultCard = ({ movie }) => {
	const { addMovieToWatchlist, addMovieToWatched, watchlist, watched } =
		useContext(GlobalContext);

	const movieInWatchlist = watchlist.find((watchlistMovie) => {
		return watchlistMovie.id === movie.id;
	});

	const movieInWatched = watched.find(
		(wathedMovie) => wathedMovie.id === movie.id
	);

	const addMovieDisabled = movieInWatchlist ? true : false;

	const addToWatchedDisabled = movieInWatched ? true : false;
	return (
		<div className="result-card">
			<div className="poster-wrapper">
				{movie.poster_path ? (
					<img
						src={`https://image.tmdb.org/t/p/w200${movie.poster_path}`}
						alt={`${movie.title} poster`}
					/>
				) : (
					<div className="filler-poster"></div>
				)}
			</div>
			<div className="info">
				<div className="header">
					<h3 className="title">{movie.title}</h3>
					<h4 className="release-date">
						{/* {movie.release_date ? movie.release_date.substring(0, 4) : '-'} */}
						{movie.release_date ? (
							<Moment format="yyyy">{movie.release_date}</Moment>
						) : (
							'-'
						)}
					</h4>
				</div>
			</div>
			<div className="controls">
				<button
					className="btn"
					disabled={addMovieDisabled}
					onClick={() => addMovieToWatchlist(movie)}
				>
					Add to Watchlist
				</button>
				<button
					className="btn"
					disabled={addToWatchedDisabled}
					onClick={() => addMovieToWatched(movie)}
				>
					Add to Watched
				</button>
			</div>
		</div>
	);
};
