import React, { useState } from 'react';
import { ResultCard } from './ResultCard';

export const Add = () => {
	const [query, setQuery] = useState('');
	const [results, setResults] = useState([]);

	const onQueryChangeHandler = (event) => {
		event.preventDefault();
		const movieToSearch = event.target.value;
		setQuery(movieToSearch);

		fetch(
			`https://api.themoviedb.org/3/search/movie?api_key=${process.env.REACT_APP_TMDB_KEY}&language=en-US&page=1&include_adult=false&query=${movieToSearch}`
		)
			.then((res) => res.json())
			.then((data) => {
				if (!data.errors) {
					setResults(data.results);
					console.log(results);
				} else setResults([]);
			});
	};

	return (
		<div>
			<div className="add-page">
				<div className="container">
					<div className="add-content">
						<div className="input-wrapper">
							<input
								type="text"
								placeholder="search for movie"
								value={query}
								onChange={onQueryChangeHandler}
							/>
						</div>
						{results.length > 0 && (
							<ul className="results">
								{results.map((movie) => {
									return (
										<li key={movie.id}>
											<ResultCard movie={movie} />
										</li>
									);
								})}
							</ul>
						)}
					</div>
				</div>
			</div>
		</div>
	);
};
export default Add;
